// ‡∏ü‡∏±‡∏á‡∏Å‡πå‡∏ä‡∏±‡∏ô‡∏ï‡∏£‡∏ß‡∏à‡∏™‡∏≠‡∏ö‡∏õ‡∏£‡∏∞‡πÄ‡∏†‡∏ó‡πÑ‡∏ü‡∏•‡πå
const isValidFileType = (file: File): boolean => {
  const allowedTypes = [
    'application/pdf',
    'application/msword',
    'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
    'application/vnd.ms-word',
    'application/vnd.ms-word.document.macroEnabled.12',
    'application/vnd.ms-word.template.macroEnabled.12'
  ];
  
  const fileType = file.type.toLowerCase();
  const fileName = file.name.toLowerCase();
  
  // ‡∏ï‡∏£‡∏ß‡∏à‡∏™‡∏≠‡∏ö‡∏à‡∏≤‡∏Å MIME type
  if (allowedTypes.includes(fileType)) {
    return true;
  }
  
  // ‡∏ï‡∏£‡∏ß‡∏à‡∏™‡∏≠‡∏ö‡∏à‡∏≤‡∏Å‡∏ô‡∏≤‡∏°‡∏™‡∏Å‡∏∏‡∏•‡πÑ‡∏ü‡∏•‡πå (‡∏Å‡∏£‡∏ì‡∏µ‡∏ö‡∏≤‡∏á‡πÄ‡∏ö‡∏£‡∏≤‡∏ß‡πå‡πÄ‡∏ã‡∏≠‡∏£‡πå‡∏≠‡∏≤‡∏à‡πÑ‡∏°‡πà‡πÑ‡∏î‡πâ‡∏™‡πà‡∏á MIME type ‡∏ñ‡∏π‡∏Å‡∏ï‡πâ‡∏≠‡∏á)
  if (fileName.endsWith('.pdf') || 
      fileName.endsWith('.doc') || 
      fileName.endsWith('.docx') ||
      fileName.endsWith('.dot') ||
      fileName.endsWith('.dotx')) {
    return true;
  }
  
  return false;
};

// ‡∏ü‡∏±‡∏á‡∏Å‡πå‡∏ä‡∏±‡∏ô‡∏£‡∏±‡∏ö‡∏ä‡∏∑‡πà‡∏≠‡∏õ‡∏£‡∏∞‡πÄ‡∏†‡∏ó‡πÑ‡∏ü‡∏•‡πå
const getFileTypeName = (file: File): string => {
  const fileName = file.name.toLowerCase();
  
  if (fileName.endsWith('.pdf')) return 'PDF';
  if (fileName.endsWith('.doc')) return 'DOC';
  if (fileName.endsWith('.docx')) return 'DOCX';
  if (fileName.endsWith('.dot')) return 'DOT';
  if (fileName.endsWith('.dotx')) return 'DOTX';
  
  // ‡∏ï‡∏£‡∏ß‡∏à‡∏™‡∏≠‡∏ö‡∏à‡∏≤‡∏Å MIME type
  if (file.type.includes('pdf')) return 'PDF';
  if (file.type.includes('word') || file.type.includes('msword')) return 'Word';
  
  return '‡πÄ‡∏≠‡∏Å‡∏™‡∏≤‡∏£';
};

// ‡∏ü‡∏±‡∏á‡∏Å‡πå‡∏ä‡∏±‡∏ô‡∏£‡∏±‡∏ö‡πÑ‡∏≠‡∏Ñ‡∏≠‡∏ô‡∏ï‡∏≤‡∏°‡∏õ‡∏£‡∏∞‡πÄ‡∏†‡∏ó‡πÑ‡∏ü‡∏•‡πå
const getFileIcon = (file: File): React.ReactNode => {
  const fileName = file.name.toLowerCase();
  
  if (fileName.endsWith('.pdf')) {
    return (
      <div className="relative">
        <DocumentTextIcon className="h-6 w-6 text-red-500" />
        <span className="absolute -top-1 -right-1 text-[10px] font-bold text-red-500">PDF</span>
      </div>
    );
  } else {
    return (
      <div className="relative">
        <DocumentIcon className="h-6 w-6 text-blue-500" />
        <span className="absolute -top-1 -right-1 text-[10px] font-bold text-blue-500">DOC</span>
      </div>
    );
  }
};

